@import url("https://fonts.googleapis.com/css?family=Montserrat");
* {
  margin: 0;
}

.card-container {
  box-shadow: 0px 0px 20px 0px #c4c4c440;

  display: flex;

  width: 20rem;
  /* height: 17rem; */
  border-radius: 0.6rem;
  overflow: hidden;
  font-family: "Montserrat";
  padding-bottom: 20px;
}
.card-img {
  height: 2.8rem;
  width: 2.8rem;
  margin-top: 0.5625rem;
  margin-left: 0.75rem;
  border-radius: 6.25rem;
}
.card-people {
  margin-top: 1.8rem;
  margin-left: 0.6rem;
}

.card-title {
  text-transform: capitalize;
  font-style: normal;
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.5rem;
  word-break: break-word;

  color: #333333;

  margin-left: 0.6rem;
  margin-top: -10px;
}
.card-occupation {
  margin-left: 0.6rem;
  font-style: normal;
  font-weight: 400;
  font-size: 0.75rem;
  text-align: justify;
  line-height: 1rem;

  color: #333333;
}
.card-section {
  margin-left: 0.6rem;
  margin-top: 0.75rem;

  word-break: break-word;

  text-align: center;
  font-style: normal;
  font-weight: 500;

  font-size: 0.76rem;
  line-height: 1rem;

  color: #396afc;

  width: 8.125rem;
  padding: 0.56rem;
  background: linear-gradient(
      0deg,
      rgba(57, 106, 252, 0.1),
      rgba(57, 106, 252, 0.1)
    ),
    #ffffff;
  border-radius: 0.3125rem;
}

.card-info {
  font-style: normal;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 1rem;

  color: #02014f;
}
.icon {
  position: relative;
  font-size: 1.25rem;
  margin: 0.188rem;
  top: 0.6rem;
}
